@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  padding: 20px;
}
.header__top-wrap {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
.header__toggle {
  font-size: 0;
  line-height: 0;
  color: transparent;
  width: 30px;
  height: 30px;
  background-image: url("../img/menu-open.svg");
  background-repeat: no-repeat;
  background-size: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__toggle--show {
  background-image: url("../img/menu-close.svg");
}
.header__toggle:hover,
.header__toggle:focus {
  opacity: 0.6;
  outline: none;
}
.header__toggle:active {
  opacity: 0.3;
  outline: none;
}
.languages-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
}
.languages-list__item:not(:last-child) {
  margin-right: 15px;
}
.languages-list__button {
  font-size: 0;
  line-height: 0;
  color: transparent;
  display: block;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  border-radius: 50%;
}
.languages-list__button--russian {
  background-image: url("../img/russia.svg");
}
.languages-list__button--english {
  background-image: url("../img/united-kingdom.svg");
}
.languages-list__button--japanese {
  background-image: url("../img/japan.svg");
}
.languages-list__button:hover,
.languages-list__button:focus {
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.4);
  outline: none;
}
.languages-list__button:active {
  box-shadow: 0 2px 10px 0 rgba(0, 176, 199, 0.8);
  outline: none;
}
.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nav-list_item {
  padding-top: 15px;
}
.nav-list_item:first-child {
  padding-top: 30px;
}
.nav-list_item:last-child {
  padding-bottom: 15px;
}
.nav-list_link {
  display: inline-block;
  vertical-align: middle;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
}
.nav-list_link:hover,
.nav-list_link:focus {
  color: #00b0c7;
  outline: none;
}
.nav-list_link:active {
  opacity: 0.3;
  outline: none;
}
.main-nav {
  display: block;
}
.main-nav--hidden {
  display: none;
}
.about-me {
  padding: 0 20px;
  margin-top: 40px;
}
.about-me__wrap {
  display: flex;
  align-items: center;
}
.about-me__info-wrap {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about-me__info-wrap p {
  margin: 0;
}
.tech-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.tech-list__item {
  position: relative;
  padding-left: 25px;
}
.tech-list__item:not(:last-child) {
  margin-bottom: 15px;
}
.tech-list__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 2px;
  width: 16px;
  height: 16px;
  background-image: url("../img/tick.svg");
  background-size: 16px;
  background-repeat: no-repeat;
}
.markups-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.markups-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
  margin-bottom: 20px;
}
.markups-list__item .markups-list__link:last-child {
  border-left: 1px solid #ffffff;
}
.markups-list__links-wrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.markups-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  padding: 10px 15px;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00b0c7;
}
.markups-list__link:hover,
.markups-list__link:focus {
  opacity: 0.6;
  outline: none;
}
.markups-list__link:active {
  opacity: 0.3;
  outline: none;
}
.markups-list__image {
  display: block;
}
.markups-list__description-wrap {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.markups-list__description-button {
  display: block;
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00b0c7;
  border: none;
  border-top: 1px solid #ffffff;
  cursor: pointer;
}
.markups-list__description-button:hover,
.markups-list__description-button:focus {
  opacity: 0.6;
  outline: none;
}
.markups-list__description-button:active {
  opacity: 0.3;
  outline: none;
}
.markups-list__description {
  display: block;
}
.markups-list__description--hidden {
  display: none;
}
.graphic-editors-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
.graphic-editors-list__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 140px;
  margin-bottom: 20px;
}
.graphic-editors-list__item:hover .graphic-editors-list__title {
  display: block;
}
.graphic-editors-list__title {
  position: absolute;
  top: 68px;
  z-index: 1;
  display: none;
  padding: 10px 15px;
  color: #ffffff;
  text-align: center;
  background-color: #00b0c7;
  border-radius: 4px;
}
.graphic-editors-list__title::after {
  position: absolute;
  content: "";
  z-index: 1;
  top: -8px;
  left: 50%;
  margin-left: -8px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #00b0c7;
}
.certificates-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.certificates-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}
.certificates-list__link {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.certificates-list__title {
  text-align: center;
}
.contacts {
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  margin-top: 40px;
}
.contacts__links-wrap {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}
.contacts__link {
  font-size: 0;
  line-height: 0;
  color: transparent;
  border-radius: 50%;
}
.contacts__link--stack-overflow,
.contacts__link--kwork {
  border-radius: 0;
}
.contacts__link:hover,
.contacts__link:focus {
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.4);
  outline: none;
}
.contacts__link:active {
  box-shadow: 0 2px 10px 0 rgba(0, 176, 199, 0.8);
  outline: none;
}
.contacts__link--email {
  align-self: center;
  margin-bottom: 20px;
  font-size: 16px;
  color: #00b0c7;
  line-height: 16px;
}
.contacts__link--email:hover,
.contacts__link--email:focus {
  box-shadow: none;
  opacity: 0.6;
}
.contacts__link--email:active {
  box-shadow: none;
  opacity: 0.3;
}
.what-i-use {
  margin-top: 40px;
  padding: 0 20px;
}
.heading {
  margin: 0 0 30px 0;
  text-transform: uppercase;
  text-align: center;
}
.portfolio {
  margin-top: 40px;
  padding: 0 20px;
}
.editors {
  margin-top: 40px;
  padding: 0 20px;
}
.education {
  padding: 0 20px;
  margin-top: 40px;
}
.copyrights__item {
  font-size: 8px;
}
.copyrights__link {
  font-size: 8px;
  color: #00b0c7;
}
